import lee1 from './lee1.vue'
import lee2 from './lee2.vue'
import lee3 from './lee3.vue'
import lee4 from './lee4.vue'
import lee5 from './lee5.vue'
import footers from './footers.vue'
const index = {
    install(Vue) {
        Vue.component('lee1', lee1)
        Vue.component('lee2', lee2)
        Vue.component('lee3', lee3)
        Vue.component('lee4', lee4)
        Vue.component('lee5', lee5)
        Vue.component('footers', footers)
    }
}
export default index


