<template>
  <div id="apub2pg">
    <!-- 头部 -->
    <div class="apnub2_head">
      <van-icon @touchstart="returns" name="arrow-left" class="fl" style="padding:0 4px;"/>
      <router-link to="/Hello"><van-icon name="home-o" class="fr"/></router-link>
      <h3 style="margin:0 100px;text-align:center;">{{title}}</h3>
    </div>
    <!-- 内容 -->
    <div class="list_age">
      <span @touchstart="age_a" :class="index==0?'age_active':''">男生</span>
      <span @touchstart="age_b" :class="index==1?'age_active':''">女生</span>
    </div>
    <div style="min-height:60vh;background:#fff;padding-left:20px;">
      <div v-for="(item, index) in arr" :key="index" style="padding:20px 4px 20px 0;border-bottom:1px #f0f0f0 solid;" class="clearfix">
        <router-link :to="'/all/'+item.bid">
          <div class="fl">
            <img :src="item.book_cover || item.cover" style="height:80px;width:60px;" alt="">
          </div>
          <div class="fl" style="margin-left:16px;width:270px;">
            <p style="width:80%;padding:4px 0;font-size:16px;">{{item.bookname || item.title}}</p>
            <p style="color:#666;">{{item.author_name || item.author}}
              <span class="fr"><van-icon style="font-size:19px;margin-right:4px;vertical-align:middle;" name="browsing-history-o" />{{item.extid}}</span>
            </p>
            <p style="width:50%;margin-top:10px;">
              <span style="padding:1px 6px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">完集</span>
              <span style="padding:1px 6px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">言情</span>
            </p>
          </div>
        </router-link>
      </div>
    </div> 
    <!--尾部  -->
    <div>
      <footers></footers>
    </div>
  </div>
</template>

<script>
export default {
  name:'apub2pg',
  data(){
   return {
     arr:'',
     title:'',
     cid:'',
     n:1,//这里是男生女生分类
     index:0,
   }
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
  created(){
    this.cid=this.$route.params.w;
    this.title=this.$route.query.title;
    this.allpop();
    console.log(this.cid,this.title)
  },
  methods:{
    returns(){
      this.$router.go(-1);
    },
    age_a(){ //男生
      if(this.index==0){ //降低消耗
        return
      }
      else{
        this.index=0;
        this.n=1;
        this.allpop();
      }
    },
    age_b(){ //女生
      if(this.index==1){ //降低消耗
        return
      }
      else{
        this.index=1;
        this.n=2;
        this.allpop();
      }
    },
    allpop(){
      let n;
      n=this.n;
      this.$http.get('http://read.xiaoshuo1-sm.com/novel/i.php?',{
        params:{
          do: 'is_novelrank',
          p: 1,
          page: 1,
          size: 10,
          onlyCpBooks: 1,
          gender:n,//    这里
          type:this.cid,
          shuqi_h5:'' ,
          _: 1568345133621,
        }
      }).then((res)=>{
        this.arr=res.data.data;
        console.log(res.data.data)
      })  
    }
  }
}
</script>

<style scoped>
#apub2pg{
  min-height:100vh;
  position:relative;
  background-color: #f0f0f0;
  z-index: 102;
  padding-top:80px;
}
.apnub2_head{
  padding:0 20px;
  height:80px;
  line-height:80px;
  position:fixed;
  left:0;top:0;
  width:100%;
  box-sizing: border-box;
  border-bottom:1px #e9e9e9 solid;
  background-color: #fff;
}
.apnub2_head h3{
  font-size:36px;
  font-weight:normal;
}
.apnub2_head i{
  line-height:80px;
  font-size:40px;
  color:#2dc033;
}
.item_box{
  flex:1;
  margin:0 10px;
  width:25%;
  padding-bottom:30px;
  overflow: hidden;
}
.item_box p{
  width:140px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.list_age{
  display: flex;
  background: #fff;
  border-bottom:1px #cccccc solid;
}
.list_age span{
  flex:1;
  text-align:center;
  padding:20px 0;
  font-size:30px;
}
.age_active{
  color:#2dc033;
  border-bottom:5px #2dc033 solid;
}

</style>



