<!-- 为你推荐模块 -->
<template>
    <div id="lee2">
      <div class="clearfix" style="padding:0 10px;">
          <div v-for="(item, index) in lee2_arr" :key="index" style="padding-top:20px;border-bottom:1px #f0f0f0 solid;width:100%;position:relative;" class="item_box fl clearfix">
            <router-link :to="'/all/'+item.bid">
              <div class="fl">
                <img :src="item.book_cover || item.cover" style="height:114px;width:82px;" alt="">
              </div>
              <div class="fl" style="margin-left:16px;width:240px;">
                <p style="width:80%;padding:4px 0;font-size:16px;">{{item.bookname || item.title}}</p>
                <p style="color:#666;">{{item.author_name || item.author}}</p>
                <p style="height:32px;margin-top:4px;color:#666;white-space:normal;width:100%;">{{item.book_info || item.desc}}</p>
                <p v-if="item.book_cover" style="margin-top:10px;width:90%;">
                  <span style="padding:1px 6px;display:inline-block;color:#00c98d; border:1px rgb(140, 228, 143) solid;border-radius:4px;"
                    :style="{color:item.stat_name=='连载'?'#70a7e3':'#00c98d',border:item.stat_name=='连载'?'1px #70a7e3 solid':'1px #00c98d solid'}">{{item.stat_name}}</span>
                  <span style="padding:1px 6px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">{{item.chapterNum}}万字</span>
                  <span style="padding:1px 6px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">{{item.tag[0]}}</span>
                  <span style="padding:1px 6px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">{{item.tag[1]}}</span>
                </p>
                <p v-else style="width:50%;margin-top:20px;">
                  <span style="padding:1px 6px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">完集</span>
                  <span style="padding:1px 6px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">言情</span>
                </p>
              </div>
              <span v-show="item.author_name" class="ding">最新</span>
            </router-link>
          </div>
      </div>
    </div>
</template>

<script>
export default {
  name:'lee2',
  props:['lee2_arr'],
  data(){
   return {
       arr:this.lee2_arr
   }
  },
  watch:{
    //一定要监听
    lee2_arr(a,b){
      this.arr=a;
    }
  }
}
</script>

<style scoped>
.item_box{
  flex:1;
  margin:0 10px;
  width:25%;
  padding-bottom:30px;
  overflow: hidden;
}
.item_box p{
  width:140px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.ding{
  background:#f9877f;
  text-align:center;
  line-height:40px;
  height:40px;
  width:140px;
  transform: rotate(45deg);
  z-index:10;
  font-size:20px;
  color:#fff;
  position:absolute;
  right:-40px;
  top:10px;
}
</style>



