<template>
  <div id="apub5">
    <!-- 头部 -->
    <div class="apnub5_head">
      <router-link to="/Hello"><van-icon name="arrow-left" class="fl" style="padding:0 4px;"/></router-link>
      <router-link to="/Hello"><van-icon name="home-o" class="fr"/></router-link>
      <h3 style="margin:0 100px;text-align:center;">精品书单</h3>
    </div>
    <!-- 内容 -->
    <div>
      <lee5 :lee5_arr="arr" style="min-height:70vh;"></lee5>
    </div>
    <!--尾部  -->
    <div>
      <footers></footers>
    </div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui'; //局部引入弹出框
export default {
  name:'apub5',
  data(){
   return {
     arr:'',
   }
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
  created() {
    //进入页面加载loading
    Indicator.open({
      text: '加载中...',
      spinnerType: 'snake'
    });
    this.$http.get('http://read.xiaoshuo1-sm.com/novel/i.php?do=is_pay_sdlist&page=1&size=20&shuqi_h5=&onlyCpBooks=1&_=1568275876467').then((res)=>{
      this.arr=res.data.data;
      setTimeout(() => {
        Indicator.close(); //关闭loading
      }, 300);
    })  
  },
  methods:{

  }
}
</script>

<style scoped>
#apub5{
  min-height:100vh;
  position:relative;
  background-color: #f0f0f0;
  z-index: 102;
  padding-top:80px;
}
.apnub5_head{
  padding:0 20px;
  height:80px;
  line-height:80px;
  position:fixed;
  left:0;top:0;
  width:100%;
  z-index: 20;
  box-sizing: border-box;
  border-bottom:1px #ccc solid;
  background-color: #fff;
}
.apnub5_head h3{
  font-size:36px;
  font-weight:normal;
}
.apnub5_head i{
  line-height:80px;
  font-size:40px;
  color:#2dc033;
}
</style>



