import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import bookshelf from '@/components/bookshelf'
import Hello from '@/components/Hello'
import vip from '@/components/vip'
import wo from '@/components/wo'
import apub1 from '@/components/apub1'
import apub2 from '@/components/apub2'
import apub3 from '@/components/apub3'
import apub4 from '@/components/apub4'
import apub5 from '@/components/apub5'
import all from '@/components/all'
import apub1pg from '@/components/apub1pg'
import apub2pg from '@/components/apub2pg'
import apub5pg from '@/components/apub5pg'
import search from '@/components/search'
import ahomebj from '@/components/ahomebj'
import login from '@/components/login'
import login1 from '@/components/login1'
import login2 from '@/components/login2'
import setup from '@/components/setup'

Vue.use(Router)
Vue.use(Resource)

export default new Router({
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  },
  routes: [
    {
      path:'/',
      redirect:'/ahomebj',
    },
    { 
      path: '/bookshelf',
      name: 'bookshelf',
      component: bookshelf,
    },
    { 
      path: '/ahomebj',
      name: 'ahomebj',
      component: ahomebj,
    },
    {
      path: '/Hello',
      name: 'Hello',
      component: Hello,
    },
    {
      path: '/vip',
      name: 'vip',
      component: vip
    },
    {
      path: '/wo',
      name: 'wo',
      component: wo
    },
    {
      path: '/apub1',
      name: 'apub1',
      component: apub1,
    },
    {
      path:'/apub1pg/:p',
      name:'apub1pg',
      component: apub1pg,
    },
    {
      path: '/apub2',
      name: 'apub2',
      component: apub2
    },
    {
      path: '/apub2pg/:w',
      name: 'apub2pg',
      component: apub2pg
    },
    {
      path: '/apub3',
      name: 'apub3',
      component: apub3
    },
    {
      path: '/apub4',
      name: 'apub4',
      component: apub4
    },
    {
      path: '/apub5',
      name: 'apub5',
      component: apub5
    },
    {
      path: '/apub5pg/:v',
      name: 'apub5pg',
      component: apub5pg
    },
    {
      path: '/all/:id',
      name: 'all',
      component: all
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/login1',
      name: 'login1',
      component: login1
    },
    {
      path: '/login2',
      name: 'login2',
      component: login2
    },
    {
      path: '/setup',
      name: 'setup',
      component: setup
    }
  ]
})



