<template>
    <div id="setup" style="background:#f0f0f0;min-height:94vh;">
        <div class="setup">
            <router-link to="/wo"><van-icon name="arrow-left" class="fl" style="padding:0 4px;"/></router-link>
            <h3 style="margin:0 100px;text-align:center;">设置</h3>
        </div>
        <div>
            <p class="setup_p">使用2G/3G/4G/5G同步书架<mt-switch v-model="value1" class="fr" style="margin-top:10px;"></mt-switch></p>
            <p class="setup_p">启动听书悬浮窗<mt-switch v-model="value2" class="fr" style="margin-top:10px;"></mt-switch></p>
            <p class="setup_p">自动购买设置<van-icon name="arrow" class="fr" style="line-height:40px;"/></p>
            <p class="setup_p">Wifi下自动下载已购章节<mt-switch v-model="value3" class="fr" style="margin-top:10px;"></mt-switch></p>
            <p class="setup_p" style="margin:30px 0;">消息提示设置<span class="fr">已开启</span></p>
            <p class="setup_p">清除缓存<van-icon name="arrow" class="fr" style="line-height:40px;"/></p>
            <p class="setup_p">好评支持一下<van-icon name="arrow" class="fr" style="line-height:40px;"/></p>
            <p class="setup_p">关于书旗<van-icon name="arrow" class="fr" style="line-height:40px;"/></p>
        </div>
    </div>
</template>

<script>
export default {
  name:'setup',
  data(){
   return {
       value1:'',
       value2:'',
       value3:'',
       value4:'',
   }
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
  created(){
      
  }
}
</script>

<style scoped>
#setup{
    padding-top:80px;
}
.setup{
  padding:0 20px;
  height:80px;
  line-height:80px;
  position:fixed;
  left:0;top:0;
  width:100%;
  z-index: 20;
  box-sizing: border-box;
  background-color: #fff;
}
.setup h3{
  font-size:36px;
  font-weight:normal;
}
.setup i{
  line-height:80px;
  font-size:40px;
  color:#666;
}
.setup_p{
  font-size:30px;
  padding:0 20px;
  background:#fff;
  border-bottom:1px #f0f0f0 solid;
  line-height:90px;
}
</style>



