<template>
  <div id="apub4">
    <!-- 头部 -->
    <div class="apnub4_head">
      <router-link to="/Hello"><van-icon name="arrow-left" class="fl" style="padding:0 4px;"/></router-link>
      <router-link to="/Hello"><van-icon name="home-o" class="fr"/></router-link>
      <h3 style="margin:0 100px;text-align:center;">男生频道</h3>
    </div>
    <!-- 内容 -->
    <div>
      <!-- 主编力荐 -->
      <div style="min-height:60vh;margin-bottom:4px;">
        <div style="min-height:463px;background:#fff;" class="jinp">
          <h3>主编力荐</h3>
          <lee3 :lee3_arr="arr1"></lee3> 
        </div>
        <div class="more" @touchstart="mores1" style="background:#fff;">
          换一换<van-icon name="replay" style="vertical-align: middle;padding-left:6px;" />
        </div>
      </div> 
      <!-- 男频新书 -->
      <div style="min-height:60vh;margin-bottom:4px;">
        <div style="min-height:463px;background:#fff;" class="jinp">
          <h3>男频新书</h3>
          <lee3 :lee3_arr="arr2"></lee3> 
        </div>
        <div class="more" @touchstart="mores2" style="background:#fff;">
          换一换<van-icon name="replay" style="vertical-align: middle;padding-left:6px;" />
        </div>
      </div> 
      <!-- 男频爽文榜 -->
      <div class="jinp" style="background:#fff;">
        <h3>男频爽文榜</h3>
        <div>
          <lee4 :lee4_arr="arr3"></lee4>
        </div>
        <div class="more">
          <router-link :to="{path:'/apub2pg/1',query:{title:'精品畅销榜'}}" style="color:#666;">查看更多</router-link>
        </div>
      </div>
    </div>
    <!--尾部  -->
    <div>
      <footers></footers>
    </div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui'; //局部引入弹出框
export default {
  name:'apub4',
  data(){
   return {
     arr1:'',
     arr2:'',
     arr3:'',
   }
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
  created() {
    //进入页面加载loading
    Indicator.open({
      text: '加载中...',
      spinnerType: 'snake'
    });
    this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/stencil/query?appId=1&pageId=2&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568275537&type=2&resetcache=&sign=2B9DBB4A41093667903620D391DB977E&key=shuqiapi&_=1568275537061').then((res)=>{
      this.arr1=res.data.data.module[1].content.splice(0,4);
      this.arr2=res.data.data.module[11].content.splice(0,4);
      this.arr3=res.data.data.module[16].content;
      setTimeout(() => {
        Indicator.close(); //关闭loading
      }, 300);
    })  
  },
  methods:{
    //主编力荐
    mores1(){
      this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=2&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568275558&type=2&resetcache=&func_id=19&orderid=2&mid=42&lmkTxt=listBox&sign=5260F1B05BC1AC84083FAEB5C27926D3&key=shuqiapi&_=1568275558353').then((res)=>{
        this.arr1=res.data.data.module[0].content.splice(0,4);  
      })  
    },
    //男频新书
    mores2(){
      this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=2&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568275572&type=2&resetcache=&func_id=19&orderid=12&mid=52&lmkTxt=listBox&sign=92EBC85EF6E6726D0CFE614D7E5B9BFF&key=shuqiapi&_=1568275572122').then((res)=>{
        this.arr2=res.data.data.module[0].content.splice(0,4);      
      })  
    }
  }
}
</script>

<style scoped>
#apub4{
  min-height:100vh;
  position:relative;
  background-color: #f0f0f0;
  z-index: 102;
  padding-top:80px;
}
.apnub4_head{
  padding:0 20px;
  height:80px;
  line-height:80px;
  position:fixed;
  left:0;top:0;
  width:100%;
  z-index: 20;
  box-sizing: border-box;
  border-bottom:1px #ccc solid;
  background-color: #fff;
}
.apnub4_head h3{
  font-size:36px;
  font-weight:normal;
}
.apnub4_head i{
  line-height:80px;
  font-size:40px;
  color:#2dc033;
}
.jinp h3{
  font-weight:normal;
  padding:24px 30px;
  position:relative;
}
.jinp h3:after{
  content:'';
  height:30px;
  width:6px;
  background-color: #2dc033;
  left:0;
  top:38%;
  position:absolute;
}
.more{
  height:100px;
  text-align:center;
  font-size:30px;
  line-height:100px;
  border-top:1px #ccc solid;
  border-bottom:14px #f0f0f0 solid;
  color:#666;
}
</style>



