<template>
    <div id="vip" ref="pop" style="padding-bottom:40px;">
      <div class="search_h">
        <input @focus="zou" type="text" placeholder="书名、作者名、关键字">
      </div>
      <transition name="ap">
        <div class="search_h huiy" v-show="type"> 
          <van-icon name="search" class="fr" style="line-height:30px;"/>
          <h5 style="margin-top:-4px;font-size:16px;padding:0 100px;text-align:center;">会员</h5>
        </div>
      </transition>
      <div style="margin-bottom:10px;">
        <banner></banner>
      </div>
      <div style="height:150px;padding:0 20px;">
        <div class="vip_box">
          <div class="clearfix">
            <div class="fl"><img style="height:36px;width:36px;" src="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=2435093970,3292456683&fm=26&gp=0.jpg" alt=""></div>
            <div class="fl" style="margin:0 60px 0 10px;">
              <h4 style="font-size:16px;">书友1253771253</h4>
              <p style="color:#666;">开通会员万本好书免费读</p>
            </div>
            <van-button type="primary" style="margin-top:10px;font-size:14px;">10元开通</van-button>
          </div>
          <div class="list_box">
            <div>
              <p class="p_bj"><van-icon name="bookmark-o" /></p>
              <p>万本免费</p>
            </div>
            <div>
              <p class="p_bj"><van-icon name="vip-card-o" /></p>
              <p>8折购买</p>
            </div>
            <div>
              <p class="p_bj"><van-icon name="point-gift-o" /></p>
              <p>定制礼包</p>
            </div>
            <div>
              <p class="p_bj"><van-icon name="qr-invalid" /></p>
              <p>更多特权</p>
            </div>
          </div>
        </div>
      </div>
      <div class="jinp">
        <h3>全部为你推荐</h3>
        <lee2 :lee2_arr="arr"></lee2>
      </div>
    </div>
</template>

<script>
import { Indicator } from 'mint-ui'; //局部引入弹出框
import banner from './ui/banner'
export default {
  name:'vip',
  components:{
    banner
  },
  data(){
   return {
    arr:'',
    type:false,

   }
  },
  created() {
    //进入页面加载loading
    Indicator.open({
      text: '加载中...',
      spinnerType: 'snake'
    });
    //为你推荐
    this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=1&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568213733&type=2&resetcache=&func_id=12%2C33%2C11%2C28%2C33%2C12%2C33%2C11%2C19&orderid=31%2C32%2C33%2C34%2C35%2C36%2C37%2C38%2C39&sign=76FCF7AD08095433655CECE765011CD5&key=shuqiapi&_=1568213733118').then((res)=>{    
      this.arr=res.data.data.module[8].content.splice(10);
      setTimeout(() => {
        Indicator.close(); //关闭loading
      }, 300);
    })
  },
  mounted(){
    //进入页面打开footer
    document.querySelector('.footer').style.display='block';
    window.addEventListener("scroll", this.showIcon);//滚动
  },
  methods:{
    showIcon() {
      if(!!document.documentElement.scrollTop && document.documentElement.scrollTop > 300){            
            this.type=true
        } else {
             this.type=false
        }
    },
    zou(){
      this.$router.push('/search');
    }
  }
}
</script>

<style scoped>
.search_h{
  position:fixed;
  top:0;
  left:0;
  z-index: 50;
  padding:10px 30px;
  width:100%;
  box-sizing: border-box;
}
.search_h input{
  width:100%;
  height:60px;
  border:none;
  background: rgba(255,255,255,.5);
  color:#fff;
  padding-left:20px;
  box-sizing: border-box;
  border-radius: 16px;
  font-size:30px;
}
.search_h input::-webkit-input-placeholder{
  color:#fff;
}
.huiy{
  background-color: #fff;
  height:80px;
  line-height:80px;
}
.huiy i{
  font-size:46px;
}
.ap-enter{
  opacity:0;
}
.ap-enter-active{
  transition:.5s all linear;
}
.ap-enter-to{
  opacity:1;
}
.vip_box{
  height:100%;
  width:100%;
  margin-top:-30px;
  position: relative;
  z-index: 10;
  background-color: #fff;
  padding:26px 20px;
  box-sizing: border-box;
  border-radius: 24px;
  box-shadow: 0px 0px 14px rgba(0,0,0,.5);
}
.jinp h3{
  font-weight:normal;
  padding:24px 30px;
  position:relative;
}
.jinp h3:after{
  content:'';
  height:30px;
  width:6px;
  background-color: #2dc033;
  left:0;
  top:38%;
  position:absolute;
}
.van-button--primary{
  background:#cfa20e;
  border-radius: 20px;
  border:1px solid #cfa20e;
}
.list_box{
  display:flex;
  text-align:center;
}
.list_box div{
  flex:1;
}
.list_box .p_bj{
  height:70px;
  width:70px;
  border-radius: 50%;
  text-align: center;
  margin:26px auto 20px;
  background-color: #e9fcfb;
}
.list_box .p_bj i{
  font-size:50px;
  line-height:70px;
  color:#f3e3aa;
}
</style>



