<!-- 书架 -->
<template>
    <div id="bookshelf" style="padding:40px 0;">
        <!-- 头部 -->
        <div class="bookshelf clearfix">
            <h1 class="fl" style="font-weight:normal;font-size:20px;">让阅读成为习惯</h1>
            <div class="fr h_icon">
                <router-link to="/search"><van-icon name="search"/></router-link>
            </div>
        </div>
        <!-- 内容 -->
        <div style="padding:30px 0;" v-if="arr">
            <div class="clearfix" style="padding:0 10px;">
                <div class="item_box fl" v-for="(item, index) in arr" :key="index">
                    <router-link :to="'/all/'+item.bid">
                    <img :src="item.book_cover || item.cover" style="height:120px;width:100%;" alt="">
                    <p style="padding:4px 0;">{{item.bookname}}</p>
                    <p style="color:#666;">{{item.author_name}}</p>
                    </router-link>
                </div>
                <router-link to="/Hello">
                    <div class="item_box fl" style="text-align:center;line-height:100px;font-size:30px;color:#666;height:120px;background:#f0f0f0;">+</div>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import { Indicator } from 'mint-ui'; //局部引入弹出框
export default {
  name:'',
  data(){
   return {
    arr:'',
   }
  },
  mounted(){
    //进入页面打开footer
    document.querySelector('.footer').style.display='block';
  },
  created() {
    //进入页面加载loading
    Indicator.open({
      text: '加载中...',
      spinnerType: 'snake'
    });
    this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=1&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568213733&type=2&resetcache=&func_id=12%2C33%2C11%2C28%2C33%2C12%2C33%2C11%2C19&orderid=31%2C32%2C33%2C34%2C35%2C36%2C37%2C38%2C39&sign=76FCF7AD08095433655CECE765011CD5&key=shuqiapi&_=1568213733118').then((res)=>{    
      this.arr=res.data.data.module[8].content.splice(20,7);
      setTimeout(() => {
        Indicator.close(); //关闭loading
      }, 300);
    })
  },
}
</script>

<style scoped>
.bookshelf{
  border-bottom:1px #f0f0f0 solid;
  height:80px;
  line-height:80px;
  padding:6px 30px;
  position:fixed;
  z-index: 30;
  left:0;
  top:0;
  width:calc(100% - 60px);
  background:#fff;
}
.h_icon i{
  font-size:40px;
  color:#666;
  font-weight: bold;
  padding:0 8px;
  line-height: 80px;
}
.item_box{
  margin:0 26px;
  width:25%;
  padding-bottom:30px;
}
.item_box p{
  width:140px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
</style>



