<!-- 登录 -->
<template>
    <div id="login">
        <!-- 头部 -->
        <div class="login">
            <van-icon @click="returns" name="arrow-left" class="fl" style="padding:0 4px;"/>
            <router-link to="/Hello"><van-icon name="home-o" class="fr"/></router-link>
            <h3 style="margin:0 100px;text-align:center;">登录</h3>
        </div>
        <ul class="login_list">
            <li @touchstart="idn=0" :class="idn==0?'login_active':''">账号密码</li>
            <li @touchstart="idn=1" :class="idn==1?'login_active':''">手机验证码</li>
        </ul>
        <div v-show="idn==0" style="min-height:50vh; padding:30px 20px;">
            <van-cell-group>
            <van-field
                v-model="username"
                required
                clearable
                label="用户名"
                right-icon="question-o"
                placeholder="请输入用户名"
                @click-right-icon="$toast('hello vue')"
            />
            <van-field
                v-model="password"
                type="password"
                label="密码"
                placeholder="请输入密码"
                required
            />
            </van-cell-group>
            <div style="margin-top:40px;font-size:14px;">
               <van-checkbox v-model="checked" checked-color="#07c160">
                   同意 <span style="color:#2dc033;">《阿里文学服务协议》</span>和
                   <span style="color:#2dc033;">《隐私服务协议》</span>
               </van-checkbox>
            </div>
            <mt-button size="large" style="background:#2dc033;" type="primary">登录</mt-button>
            <div style="padding:10px 0;">
                <span class="fl">
                    <router-link to="/login2" style="color:#666;font-size:16px;">忘记密码</router-link>
                </span>
                <span class="fr">
                    <router-link to="/login1" style="color:#2dc033;font-size:16px;">注册</router-link>
                </span>
            </div>
        </div>
        <div v-show="idn==1" style="min-height:50vh; padding:30px 20px;">
             <van-cell-group>
                <van-field
                    v-model="phone"
                    label="手机号"
                    placeholder="请输入手机号"
                    error-message=""
                />
                <van-field
                    v-model="sms"
                    center
                    clearable
                    label="校验码"
                    placeholder="请输入短信验证码"
                >
                    <van-button slot="button" size="small" type="primary">发送验证码</van-button>
                </van-field>
            </van-cell-group>
            <div style="margin-top:40px;font-size:14px;">
               <van-checkbox v-model="checked1" checked-color="#07c160">
                   同意 <span style="color:#2dc033;">《阿里文学服务协议》</span>和
                   <span style="color:#2dc033;">《隐私服务协议》</span>
               </van-checkbox>
            </div>
            <mt-button size="large" style="background:#2dc033;" type="primary">登录</mt-button>
            <div style="padding:10px 0;">
                <span class="fl">
                    <router-link to="/login2" style="color:#666;font-size:16px;">忘记密码</router-link>
                </span>
                <span class="fr">
                    <router-link to="/login1" style="color:#2dc033;font-size:16px;">注册</router-link>
                </span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name:'login',
  data(){
   return {
       idn:0,
       username:'',
       password:'',
       checked:'',
       phone:'',
       sms:'',
       checked1:'',
   }
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
  methods: {
    returns(){
        this.$router.push('/wo')
    }
  },
}
</script>

<style scoped>
#login{
    padding-top:80px;
}
.login{
  padding:0 20px;
  height:80px;
  line-height:80px;
  position:fixed;
  left:0;top:0;
  width:100%;
  z-index: 20;
  box-sizing: border-box;
  border-bottom:1px #ccc solid;
  background-color: #fff;
}
.login h3{
  font-size:36px;
  font-weight:normal;
}
.login i{
  line-height:80px;
  font-size:40px;
  color:#2dc033;
}
.login_list{
    display: flex;
}
.login_list li{
    flex:1;
    text-align:center;
    font-size:32px;
    padding:24px 0;
    border-bottom:2px #f0f0f0 solid;
}
.login_active{
    border-bottom:2px #2dc033 solid !important;
    color:#2dc033;
}
.van-cell{
    font-size:30px;
    line-height:50px;
}
.van-checkbox{
    height:40px !important;
}
#login .mint-button {
    height:80px !important;
    font-size:30px;
    margin-top:100px;
}
body .van-field__label{
    width:100px !important;
    padding:0 20px;
}
</style>



