<template>
  <div id="apub1">
    <!-- 头部 -->
    <div class="apnub1_head">
      <router-link to="/Hello">
        <van-icon name="arrow-left" class="fl" style="padding:0 4px;"/> 
      </router-link>
      <router-link to="/Hello"><van-icon name="home-o" class="fr"/></router-link>
      <h3 style="margin:0 100px;text-align:center;">分类</h3>
    </div>
    <!-- 内容 -->
    <div style="min-height:80vh;">
      <!-- 女生分类 -->
      <div>
        <p style="padding:10px;color:#666;background:#fafafa;">女生分类</p>
        <div v-for="(item, index) in json.girl" :key="index" style="padding-left:10px;">  
            <div class="clearfix age_item">
              <div :style="{backgroundColor:random()}" class="bj_apub fl"></div>
                <div v-for="(i,key, index) in item" :key="index" class="fl">
                  <router-link :to="{path:'/apub1pg/'+i.cid,query:{title:key}}">
                    <p style="font-size:14px;padding-left:4px;margin-bottom:4px;">{{key}}</p>
                    <p>
                      <span v-for="(o, index) in i.list" :key="index"
                        style="padding:0 4px;color:#666;height:14px;position:relative;"
                        :class="index>0?'age_icon':''">{{o}}</span>
                    </p>
                  </router-link>
                </div>
            </div>
        </div>
      </div>
      <!-- 男生分类 -->
      <div>
        <p style="padding:10px;color:#666;background:#fafafa;">男生分类</p>
        <div v-for="(item, index) in json.boy" :key="index" style="padding-left:10px;">
          <div class="clearfix age_item">
            <div :style="{backgroundColor:random()}" class="bj_apub fl"></div>
            <div v-for="(i,key, index) in item" :key="index" class="fl">
              <router-link :to="{path:'/apub1pg/'+i.cid,query:{title:key}}">
                <p style="font-size:14px;padding-left:4px;margin-bottom:4px;">{{key}}</p>
                <p>
                  <span v-for="(o, index) in i.list" :key="index"
                    style="padding:0 4px;color:#666;height:14px;position:relative;"
                    :class="index>0?'age_icon':''">{{o}}</span>
                </p>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--尾部  -->
    <div>
      <footers></footers>
    </div>  
  </div>
</template>

<script>
import { Indicator } from 'mint-ui'; //局部引入弹出框
export default {
  name:'apub1',
  data(){
   return {
     json:'',
   }
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
  created() {
    //进入页面加载loading
    Indicator.open({
      text: '加载中...',
      spinnerType: 'snake'
    });
    this.$http.post('http://walden1.shuqireader.com/webapi/rank/classrelation?_=1568256123304',{
      type: 1,
      timestamp: 1568256123303,
      sign: 'b3dbad9bbb00633f85801bd396ce91df',
      shuqi_h5: '',
    },{
      emulateJSON:true
    }).then((res)=>{
      this.json=res.data.data;
      setTimeout(() => {
        Indicator.close(); //关闭loading
      }, 300);
    })  
  },
  methods:{
    random(){
      return `rgb(${parseInt(Math.random()*255)},${parseInt(Math.random()*255)},${parseInt(Math.random()*255)})`;
    }
  }
}
</script>

<style scoped>
#apub1{
  min-height:100vh;
  background-color: #fff;
  padding-top:80px;
}
.apnub1_head{
  padding:0 20px;
  height:80px;
  line-height:80px;
  position:fixed;
  background-color: #fff;
  z-index: 10;
  left:0;top:0;
  width:100%;
  box-sizing: border-box;
}
.apnub1_head h3{
  font-size:36px;
  font-weight:normal;
}
.apnub1_head i{
  line-height:80px;
  font-size:40px;
  color:#2dc033;
}
.bj_apub{
  height:70px;
  width:70px;
  background-color: tomato;
  border-radius: 50%;
  margin-right:26px;
}
.age_item{
  padding:24px 0;
  border-bottom:1px #f0f0f0 solid;
}
.age_icon:after{
  content:'';
  height:24px;
  width:1px;
  position:absolute;
  background-color: #666;
  left:0;top:6px;
}
</style>



