<template>
  <div id="apub1pg">
    <!-- 头部 -->
    <div class="apnub1_head">
      <router-link to="/apub1">
        <van-icon name="arrow-left" class="fl" style="padding:0 4px;"/> 
      </router-link>
      <router-link to="/Hello"><van-icon name="home-o" class="fr"/></router-link>
      <h3 style="margin:0 100px;text-align:center;">{{title}}</h3>
    </div>
    <!-- 内容 -->
    <div style="min-height:80vh;">
      <div style="padding-left:20px;border-bottom:8px #f0f0f0 solid;">
        <div class="all_list" style="border-top:1px #f0f0f0 solid;">
          <span @touchstart="quan" :style="{color:index==-1?'#2dc033':'#666'}">全部</span>
          <span @touchstart="app(item,$index)"
              v-for="(item, $index) in arr" :key="$index"
              :style="{color:index==$index?'#2dc033':'#666'}">
            {{item.name}}
          </span>
        </div>
        <div class="all_list" style="border-top:1px #f0f0f0 solid;">
          <span @touchstart="quan1" :style="{color:index1==-1?'#2dc033':'#666'}" style="color:#2dc033;">全部</span>
          <span @touchstart="quan2" :style="{color:index1==0?'#2dc033':'#666'}">20万字</span>
          <span @touchstart="quan3" :style="{color:index1==1?'#2dc033':'#666'}">20-100万字</span>
          <span @touchstart="quan4" :style="{color:index1==2?'#2dc033':'#666'}" style="padding-right:0;">100万字以上</span>
        </div>
        <div class="all_list" style="border-top:1px #f0f0f0 solid;">
          <span @touchstart="index2=-1" :style="{color:index2==-1?'#2dc033':'#666'}" style="color:#2dc033;">最热</span>
          <span @touchstart="index2=0" :style="{color:index2==0?'#2dc033':'#666'}">最新</span>
          <span @touchstart="index2=1" :style="{color:index2==1?'#2dc033':'#666'}">完集</span>
        </div>
      </div>
      <div v-if="beiy" style="font-size:18px;padding:10px;text-decoration: underline;color:tomato;">
        该列表下暂时没有小说,换一个试试
      </div>
      <div v-else>
        <div>
          <lee2 :lee2_arr="arr1"></lee2>
        </div>
      </div>
    </div>
    <!--尾部  -->
    <div>
      <footers></footers>
    </div>  
  </div>
</template>

<script>
export default {
  name:'apub1pg',
  data(){
   return {
     arr:'',
     cid:'',
     title:'',
     index:-1,
     arr1:'',
     beiy:false,
     index1:-1,
     n:'',//字数
     index2:-1,
   }
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
  created(){
    this.cid=this.$route.params.p;
    this.title=this.$route.query.title;
    this.$http.post('http://walden1.shuqireader.com/webapi/rank/classrelation?',{
      type: 2,
      cid: this.cid,
      timestamp: 1568337321667,
      sign: '6b6770602ebbdcce43e7655f54f0a7c4',
      shuqi_h5: '',
    },{emulateJSON:true}).then((res)=>{
      this.arr=res.data.data.class;
      let datas=res.data.data.tag;
      for(var i=0; i<datas.length; i++){
        this.arr.push(datas[i]);
      }
    }) 
    this.n='';
    this.publc();
  },
  methods:{
    //全部
    quan(){
      this.index=-1;
      this.n='';
      this.publc();
    },
    quan1(){
      this.index1=-1;
      this.n='';
      this.publc();
    },
    quan2(){
      this.index1=0;
      this.n=1;
      this.publc();
    },
    quan3(){
      this.index1=1;
      this.n=2;
      this.publc();
    },
    quan4(){
      this.index1=2;
      this.n=3;
      this.publc();
    },
    publc(){
      let n;
      n=this.n;
      this.$http.get('http://read.xiaoshuo1-sm.com/novel/i.php?',{
        params:{
          do: 'is_caterank',
          p: 1,
          page: 1,
          words:n,
          shuqi_h5:'' ,
          onlyCpBooks: 1,
          secondCate: this.title,
          sort: 'monthHot',
          _: 1568341351620,
        }
      }).then((res)=>{
        this.arr1=res.data.data;
      }) 
    },
    app(o,i){
      this.index=i;
      console.log(o)
      this.$http.get('http://read.xiaoshuo1-sm.com/novel/i.php?',{
        params:{
          do: 'is_caterank',
          p: 1,
          page: 1,
          words:'' ,
          shuqi_h5:'' ,
          onlyCpBooks: 1,
          secondCate: o.name,
          sort: 'monthHot',
          _: 1568342869499,
        }
      }).then((res)=>{
        if(res.data.data.length==0){
          this.beiy=true;
        }else{
          this.beiy=false;
          this.arr1=res.data.data;
          
        }
      }) 
    }
  }
}
</script>

<style scoped>
#apub1pg{
  
  min-height:100vh;
  background-color: #fff;
  padding-top:80px;
}
.apnub1_head{
  padding:0 20px;
  height:80px;
  line-height:80px;
  position:fixed;
  background-color: #fff;
  z-index: 10;
  left:0;top:0;
  width:100%;
  box-sizing: border-box;
}
.apnub1_head h3{
  font-size:36px;
  font-weight:normal;
}
.apnub1_head i{
  line-height:80px;
  font-size:40px;
  color:#2dc033;
}
.all_list{
  padding:16px 42px 16px 0;
}
.all_list span{
  display: inline-block;
  padding:4px 47px 4px 0;
  font-size:30px;
  color:#666;
}
</style>



