<!-- 书城 -->
<template>
  <div class="hello" style="padding-bottom:40px;">
    <!-- 头部 -->
    <div class="header clearfix">
      <h1 class="fl" @touchstart="reloads">
        <span><img src="http://c-shuqi.11222.cn/assets/logo_7a44de2.png" alt=""></span>
        <span><img src="http://c-shuqi.11222.cn/assets/website-name_c08f935.png" alt=""></span>
      </h1>
      <div class="fr h_icon">
        <router-link to="/login"><van-icon name="manager-o" /></router-link>
        <router-link to="/search"><van-icon name="search"/></router-link>
      </div>
    </div>
    <!-- 列表 -->
    <div class="clearfix" style="margin-top:46px;display:flex;width:100%;padding:8px 0;border-bottom:#f8f6f6 solid;"> 
      <div @touchstart="all(index)" v-for="(item, index) in list" :key="index" class="fl items">
        <div style="height:42px;width:42px;overflow:auto;margin:6px auto 10px;">
          <img :src="img" alt="" style="width:42px;"
            :style="{marginTop:-index*42+'px'}">
        </div>
        <p style="color:#666;">{{item.title}}</p>
      </div>
    </div>
    <div>
      <p style="padding:0 14px;line-height:40px; color:#666;">最近阅读：诛仙神尊 <van-icon name="arrow" class="fr" style="line-height:40px;"/></p>
      <a :href="img1_link"><img :src="img_1" style="width:100%;height:60px;" alt=""></a>
    </div>
    <!-- 精品 -->
    <div class="jinp">
      <h3>精品推荐</h3>
      <div style="min-height:170px;">
        <lee1 :arr_list="arr"></lee1>
      </div>
      <div class="more" @touchstart="mores">
        换一换<van-icon name="replay" style="vertical-align: middle;padding-left:6px;" />
      </div>
    </div>
    <!-- 女生 -->
    <div style="min-height:490px;" class="jinp">
      <h3>女生美文</h3>
      <lee3 :lee3_arr="arr1"></lee3> 
    </div>
    <div class="more" @touchstart="mores1">
      换一换<van-icon name="replay" style="vertical-align: middle;padding-left:6px;" />
    </div>
    <!-- 男生 -->
    <div style="min-height:490px;" class="jinp">
      <h3>男生热文</h3>
      <lee3 :lee3_arr="arr2"></lee3> 
    </div>
    <div class="more" @touchstart="mores2">
      换一换<van-icon name="replay" style="vertical-align: middle;padding-left:6px;" />
    </div>
    <!-- 新书专区 -->
    <div class="jinp">
      <h3>新书专区</h3>
      <div>
        <lee4 :lee4_arr="arr3"></lee4> 
      </div>
      <div class="more">
        <router-link :to="{path:'/apub2pg/5',query:{title:'新书潜力榜'}}" style="color:#666;">查看更多</router-link>
      </div>
    </div>
    <!-- 书旗畅销榜 -->
    <div class="jinp">
      <h3>书旗畅销榜</h3>
      <div>
        <lee4 :lee4_arr="arr4"></lee4> 
      </div>
      <div class="more">
        <router-link :to="{path:'/apub2pg/1',query:{title:'精品畅销榜'}}" style="color:#666;">查看更多</router-link>
      </div>
    </div>
    <!-- 精选书单 -->
    <div class="jinp">
      <h3>精选书单</h3>
      <div>
        <lee5 :lee5_arr="arr6"></lee5>
      </div>
      <div class="more">
        <router-link to="/apub5">查看更多</router-link>
      </div>
    </div>
    <!-- 为你推荐 -->
    <div class="jinp">
      <h3>根据你的阅读兴趣为你推荐</h3>
      <div style="min-height:140px;">
        <lee2 :lee2_arr="arr5"></lee2>
      </div>
    </div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui'; //局部引入弹出框
// import lee1 from './public/lee1'
export default {
  name: 'Hello',
  // components:{
  //   lee1
  // },
  data () {
    return {
      list:'',
      img:'',
      img_1:'',
      img1_link:'',
      arr:'',
      arr1:'',//女生
      arr2:'',//男生
      arr3:'',//新书专区
      arr4:'',//书旗畅销榜
      arr5:'',//为你推荐
      arr6:'',//精选书单
    }
  },
  mounted(){
    //进入页面打开footer
    document.querySelector('.footer').style.display='block';
  },
  created(){
    //进入页面加载loading
    Indicator.open({
      text: '加载中...',
      spinnerType: 'snake'
    });
    //头部列表
    this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=1&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568199336&type=2&resetcache=&func_id=24%2C20%2C11%2C19%2C33&orderid=1%2C3%2C4%2C6%2C7&sign=13D5735D8E8BF076892DDFE9766BB74E&key=shuqiapi&_=1568199336453').then((res)=>{
      let all=res.data.data.module;
      this.list=all[0].content;
      this.img=all[0].icon_img_url;
      this.img_1=all[1].content[0].image_url;
      this.img1_link=all[1].content[0].link_url;
      this.arr=all[3].content;
      setTimeout(() => {
        Indicator.close(); //关闭loading
      }, 300);
    })
    //女生美文 男生热文
    this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=1&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568205506&type=2&resetcache=&func_id=11%2C33%2C11%2C19%2C33%2C11%2C33%2C11%2C19%2C33&orderid=8%2C9%2C10%2C11%2C12%2C13%2C14%2C15%2C16%2C17&sign=C05BC84F6B3FB328C9F3B50DAE11945A&key=shuqiapi&_=1568205506464').then((res)=>{
      this.arr1=res.data.data.module[3].content.splice(0,4);//女生
      this.arr2=res.data.data.module[8].content.splice(0,4);//男生
    })
    //新书专区 书旗畅销榜
    this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=1&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568210263&type=2&resetcache=&func_id=11%2C33%2C11%2C19%2C33%2C12%2C33%2C11%2C19%2C33&orderid=18%2C19%2C23%2C24%2C25%2C26%2C27%2C28%2C29%2C30&sign=7A1B8567AE816E0822038F8C62F6862E&key=shuqiapi&_=1568210263265').then((res)=>{    
      this.arr3=res.data.data.module[3].content;//新书
      this.arr4=res.data.data.module[8].content;//新书
    })
    //精选书单
    this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=1&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568278464&type=2&resetcache=&func_id=12%2C33%2C11%2C28%2C33%2C12%2C33%2C11%2C19&orderid=31%2C32%2C33%2C34%2C35%2C36%2C37%2C38%2C39&sign=A04097E78275424B9A8E113B69BE5FBA&key=shuqiapi&_=1568278464033').then((res)=>{    
      this.arr6=res.data.data.module[3].content.data;
    })
    //为你推荐
    this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=1&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568213733&type=2&resetcache=&func_id=12%2C33%2C11%2C28%2C33%2C12%2C33%2C11%2C19&orderid=31%2C32%2C33%2C34%2C35%2C36%2C37%2C38%2C39&sign=76FCF7AD08095433655CECE765011CD5&key=shuqiapi&_=1568213733118').then((res)=>{    
      this.arr5=res.data.data.module[8].content.splice(0,10);
    })
  },
  methods:{
    reloads(){
      location.reload();
    },
    //再来一组数据
    mores(){
      this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=1&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568202165&type=2&resetcache=&func_id=19&orderid=6&mid=6&lmkTxt=index&sign=767C55DE72C1DE4C97DE7893F6F5D5BB&key=shuqiapi&_=1568202165942').then((res)=>{
          this.arr=res.data.data.module[0].content;
      })
    },
    //女生
    mores1(){
      this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=1&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568207333&type=2&resetcache=&func_id=19&orderid=11&mid=12&lmkTxt=index&sign=A582327D94196DEED06926AC65F673C5&key=shuqiapi&_=1568207333092').then((res)=>{
        this.arr1=res.data.data.module[0].content.splice(0,4);  
      })  
    },
    //男生
    mores2(){
      this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/module/query?appId=1&pageId=1&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568210119&type=2&resetcache=&func_id=19&orderid=16&mid=17&lmkTxt=index&sign=135AEA1448A17A07E2BC583EA4595CFC&key=shuqiapi&_=1568210119052').then((res)=>{
        this.arr2=res.data.data.module[0].content.splice(0,4);
      })  
    },
    //路由跳转
    all(index){
      if(index==0){
        this.$router.push('/apub1')
      }
      if(index==1){
        this.$router.push('/apub2')
      }
      if(index==2){
        this.$router.push('/apub3')
      }
      if(index==3){
        this.$router.push('/apub4')
      }
      if(index==4){
        this.$router.push('/apub5')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header{
  border-bottom:1px #f0f0f0 solid;
  height:80px;
  line-height:80px;
  padding:6px 30px;
  position:fixed;
  z-index: 30;
  left:0;
  top:0;
  width:calc(100% - 60px);
  background:#fff;
}
.header h1 span:nth-of-type(1) img{
  height:55px;
  width:55px;
  vertical-align: middle;
}
.header h1 span:nth-of-type(2) img{
 height: 36px;
 width:170px;
  vertical-align: middle;
}
.h_icon i{
  font-size:40px;
  color:#2dc033;
  padding:0 8px;
  line-height: 80px;
}
.items{
  text-align:center;
  flex:1;
}
.jinp h3{
  font-weight:normal;
  padding:24px 30px;
  position:relative;
}
.jinp h3:after{
  content:'';
  height:30px;
  width:6px;
  background-color: #2dc033;
  left:0;
  top:38%;
  position:absolute;
}
.more{
  height:100px;
  text-align:center;
  font-size:30px;
  line-height:100px;
  border-top:1px #ccc solid;
  border-bottom:14px #f0f0f0 solid;
  color:#666;
}
</style>



