<template>
  <div id="app">  
    <!-- 内容 -->
    <div>
      <transition name="lee">
        <router-view/>
      </transition>
    </div>
    <!-- 尾部 -->
    <div class="footer">
      <div style="display:flex;">
        <div class="footer_fo">
          <router-link :class="type=='/bookshelf'?'f_active':''" to="/bookshelf">
            <p><van-icon name="column"></van-icon></p>
            <p>书架</p>
          </router-link>
        </div>
        <div class="footer_fo">
          <router-link :class="type=='/Hello'?'f_active':''" to="/Hello">
            <p><van-icon name="bookmark"></van-icon></p>
            <p>书城</p>
          </router-link>
        </div>
        <div class="footer_fo">
          <router-link :class="type=='/vip'?'f_active':''" to="/vip">
            <p><van-icon name="diamond"></van-icon></p>
            <p>会员</p>
          </router-link>
        </div>
        <div class="footer_fo">
          <router-link :class="type=='/wo'?'f_active':''" to="/wo">
            <p><van-icon name="manager"></van-icon></p>
            <p>我的</p>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      type:'',
    }
  },
  mounted() {
    this.type=this.$route.path;
  },
  watch:{
    '$route'(a,b){
      this.type=a.path;
    }
  }
}
</script>

<style>
*{margin:0;padding:0; list-style-type: none;}
a{text-decoration: none; color:#000;}
.clearfix:after, .clearfix:before{content:'';display:table;}
.clearfix:after{clear:both;}
.clear{zoom:1;}
.fl{float:left;}
.fr{float:right;}
.lee-enter{
  opacity:0;
}
.lee-enter-active{
  transition:.5s all linear;
}
.lee-enter-to{
  opacity:1;
}
.footer{
  background-color: #f8f6f6;
  height:100px;
  z-index: 101;
  position:fixed;
  left:0;
  bottom:0;
  width:100%;
}
.footer_fo{
  flex:1;
  float: left;
  text-align:center;
  padding-top:10px;
  box-sizing: border-box;
}
.footer_fo p i{
  font-size:40px;
}
.footer_fo a{
  font-size:28px;
  color:#666;
}
.footer .f_active{
  color:#2dc033 !important;
}
/* loading */
.mint-indicator-wrapper{
  z-index: 1000 !important;
}
.mint-indicator{
  position:relative;
  z-index: 1000 !important;
}
</style>



