<!-- 注册 -->
<template>
    <div id="login2">
        <!-- 头部 -->
        <div class="login2">
            <router-link to="/login"><van-icon name="arrow-left" class="fl" style="padding:0 4px;"/></router-link>
            <router-link to="/Hello"><van-icon name="home-o" class="fr"/></router-link>
            <h3 style="margin:0 100px;text-align:center;">密码找回</h3>
        </div>
        <div style="min-height:50vh; padding:30px 20px;">
            <van-cell-group>
                <van-field
                    v-model="phone"
                    label="手机号"
                    placeholder="请输入手机号"
                    error-message=""
                />
                <van-field
                    v-model="sms"
                    center
                    clearable
                    label="校验码"
                    placeholder="请输入短信验证码"
                >
                    <van-button slot="button" size="small" type="primary">发送验证码</van-button>
                </van-field>
            </van-cell-group>
            <div style="margin-top:40px;font-size:14px;">
               <van-checkbox v-model="checked" checked-color="#07c160">
                   同意 <span style="color:#2dc033;">《阿里文学服务协议》</span>和
                   <span style="color:#2dc033;">《隐私服务协议》</span>
               </van-checkbox>
            </div>
            <mt-button size="large" style="background:#f0f0f0;color:#ccc;" type="primary">获取验证码</mt-button>
        </div>
    </div>
</template>

<script>
export default {
  name:'login2',
  data(){
   return {
       phone:'',
       sms:'',
       checked:'',
   }
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
}
</script>

<style scoped>
#login2{
    padding-top:80px;
}
.login2{
  padding:0 20px;
  height:80px;
  line-height:80px;
  position:fixed;
  left:0;top:0;
  width:100%;
  z-index: 20;
  box-sizing: border-box;
  border-bottom:1px #ccc solid;
  background-color: #fff;
}
.login2 h3{
  font-size:36px;
  font-weight:normal;
}
.login2 i{
  line-height:80px;
  font-size:40px;
  color:#2dc033;
}
.van-cell{
    font-size:30px;
    line-height:50px;
}
.van-checkbox{
    height:40px !important;
}
#login2 .mint-button {
    height:80px !important;
    font-size:30px;
    margin-top:100px;
}
.van-button--small{
    padding:10px 20px;
    font-size:30px;
    height:auto;
    border-radius: 16px;
}
</style>



