<template>
  <div id="search" style="padding-top:52px;">
    <!-- 搜索 -->
    <div class="search_ipt" style="padding:10px 12px;">
        <van-icon name="search"/>
        <input autofocus @change="add" v-model="val" type="text" placeholder="书名/作者/关键词">
        <button @click="onCancel">取消</button>
    </div>
    
    <!--  -->
    <div v-if="mins_list">
        <div style="border-bottom:6px #f0f0f0 solid;border-top:1px #f0f0f0 solid;padding:10px 0 0 16px;">
            <div class="con_t">   
                <div class="clearfix">
                    <router-link :to="'/all/'+mins.bid">
                        <div class="fl"><img class="img_s" :src="mins.cover" alt=""></div>
                        <div class="fl" style="width:62%;">
                            <h4 style="font-size:16px;padding-bottom:5px;font-weight:normal;">{{mins.title}}</h4>
                            <P class="al_p" style="margin-bottom:5px;">{{mins.author}}</P>
                            <p class="fl" style="">
                                <span style="padding:2px 8px;display:inline-block;color:#2dc033;border:1px #2dc033 solid;border-radius:4px;">完集</span>
                                <span style="padding:2px 8px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">30万字</span>
                                <span style="padding:2px 8px;display:inline -block;color:#ccc;border:1px #ccc solid;border-radius:4px;">古风</span>
                            </p>
                        </div>
                    </router-link>
                    <span class="fr" style="margin-top:34px;color:#ccc;border-radius:4px;padding:4px 6px; background:#e6e7ed;font-size:12px;">已添加</span>  
                </div>     
                <div class="search_btn">
                    <van-button type="primary" style="margin-right:10px;">继续阅读</van-button>
                    <van-button type="primary">查看目录</van-button>
                </div>
            </div>
            <div class="con_two" style="border-top:1px #f0f0f0 solid;">
                <van-icon name="underway-o" style="font-size:24px;vertical-align: middle;"/>
                <span style="padding:0 10px;font-size:15px;">末章</span>
                <span style="color:#666;font-size:13px;">第四百三十五章全文完</span>
                <van-icon name="arrow" class="fr" style="margin-top:4px;"/>
            </div>
        </div>   
        <div style="padding-left:16px;">
            <div v-for="(item, $index) in mins_list" :key="$index" class="con_t">   
                <div class="clearfix">
                    <router-link :to="'/all/'+item.bid">
                        <div class="fl"><img class="img_s" :src="item.cover" alt=""></div>
                        <div class="fl" style="width:62%;">
                            <h4 style="font-size:16px;padding-bottom:5px;font-weight:normal;">{{item.title}}</h4>
                            <P class="al_p" style="margin-bottom:5px;">{{item.author}}</P>
                            <p class="fl" style="">
                                <span style="padding:2px 8px;display:inline-block;color:#2dc033;border:1px #2dc033 solid;border-radius:4px;">完集</span>
                                <span style="padding:2px 8px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">30万字</span>
                                <span style="padding:2px 8px;display:inline -block;color:#ccc;border:1px #ccc solid;border-radius:4px;">古风</span>
                            </p>
                        </div>
                    </router-link>
                    <span class="fr" style="margin-top:34px;color:#666;border-radius:4px;padding:4px 6px; background:#e6e7ed;font-size:12px;">加书架</span>
                </div>     
            </div>
        </div>
        <!--尾部  -->
        <div>
            <footers></footers>
        </div>
    </div>
    <!--  -->

    <div v-else>
        <div style="padding:16px 12px;" class="tuij">
            <span v-for="(item, index) in arr" :key="index">
                <router-link :style="{color:random()}"
                    :to="'/all/+item.bookid'">
                    {{item.title}}
                </router-link>
            </span>
        </div>
        <div style="padding:0 12px;">
            <div class="more" @touchstart="mores">
                换一批<van-icon name="replay" style="vertical-align: middle;padding-left:6px;" />
            </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "search",
  data() {
    return {
      arr:'',
      val:'',
      mins:'',
      mins_list:'',
    };
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
  created(){
    this.publc();
  },
  methods:{
    publc(){
        this.$http.get('http://read.xiaoshuo1-sm.com/novel/i.php?do=is_payreco&id=8000000&qtf=shuqiApp&qtn=cpSearchReplace_sug&nums=8&shuqi_h5=&_=1568358762477').then((res)=>{    
            this.arr=res.data.data;
        })
    },
    onCancel(){
        this.$router.go(-1);
    },
    random(){
      return `rgb(${parseInt(Math.random()*255)},${parseInt(Math.random()*255)},${parseInt(Math.random()*255)})`;
    },
    //更换推荐
    mores(){
        this.publc();
    },
    //搜索
    add(){
        this.$http.get('http://read.xiaoshuo1-sm.com/novel/i.php?',{
            params:{
                do: 'is_serchpay',
                page: 1,
                size: 10,
                q:this.val,
                filterMigu: 1,
                p: 3,
                shuqi_h5: '',
                _: 1568360354350
            }
        }).then((res)=>{    
            this.mins=res.data.aladdin;
            this.mins_list=res.data.data
        })
    }
  }
};
</script>

<style scoped>
.search_ipt{
    position: fixed;
    left:0;
    top:0;
    width:100%;
    z-index: 30;
    background-color: #fff;
}
.search_ipt input{
    width:81%;
    outline: none;
    border:none;
    background-color: #f0f0f0;
    border-radius: 20px;
    height:64px;
    padding-left:60px;
    box-sizing:border-box;
    font-size:28px;
    position: relative;
    margin-left:6px;
}
.search_ipt i{
    position: absolute;
    left:34px;
    top:34px;
    font-size:40px;
    z-index: 4;
}
.search_ipt button{
    width:14%;
    outline: none;
    border:none;
    font-size:30px;
    color:#2dc033;
    background-color: transparent;
}
.tuij span{
    display:inline-block;
    padding:10px 20px;
    margin:0 6px 6px 0;
    background-color: #f3f3f3;
}
.more{
  height:100px;
  text-align:center;
  font-size:30px;
  line-height:100px;
  border-top:1px #ccc solid;
  color:#666;
}
.con_t{
  background-color: #fff;
  border-bottom:1px #ccc solid;
  padding:30px;
  padding-left:0;
}
.img_s{
  height:160px;
  width:120px;
  margin-right:34px;
}
.al_p{
  color:#666;
  line-height:50px;
  font-size:28px;
}
.search_btn{
    margin-top:30px;
}
.search_btn button{
    width:47%;
    height:70px;
    border-radius: 12px;
    font-size:30px;
}
.search_btn button:nth-of-type(2){
    background-color: #fff;
    color:#2dc033;
}
.con_two{
  padding:26px 14px 26px 0;
}
</style>



