<!-- 精品推荐模块 -->
<template>
    <div id="lee1">
        <div class="clearfix" style="display:flex;padding:0 10px;">
            <div class="item_box fl" v-for="(item, index) in arr" :key="index">
                <router-link :to="'/all/'+item.bid">
                  <img :src="item.book_cover || item.cover" style="height:120px;width:100%;" alt="">
                  <p style="padding:4px 0;">{{item.bookname}}</p>
                  <p style="color:#666;">{{item.author_name}}</p>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name:'lee1',
  props:['arr_list'],
  data(){
   return {
       arr:this.arr_list
   }
  },
  watch:{
    //一定要监听
    arr_list(a,b){
      this.arr=a;
    }
  }
}
</script>

<style scoped>
.item_box{
  flex:1;
  margin:0 10px;
  width:25%;
  padding-bottom:30px;
}
.item_box p{
  width:140px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
</style>



