<!-- 新书专区模块 -->
<template>
  <div>
    <div style="min-height:140px;">
      <div class="clearfix" style="display:flex;padding:0 10px;">
          <div class="item_box fl clearfix" v-show="index<1" v-for="(item, index) in arr4" :key="index">
            <router-link :to="'/all/'+item.bid">
              <div class="fl" style="position:relative;">
                <img :src="item.book_cover" style="height:114px;width:82px;" alt="">
                <span style="background:orange;text-align:center;line-height:30px;height:30px;width:20px;z-index:10;color:#fff;position:absolute;left:0;top:0;">1</span>
              </div>
              <div class="fl" style="margin-left:16px;width:240px;">
                <p style="padding:4px 0;font-size:16px;">{{item.bookname}}</p>
                <p style="color:#666;">{{item.author_name}}</p>
                <p style="overflow:auto;margin:4px 0 10px 0;max-height:34px;color:#666;white-space:normal;width:100%;">
                  {{item.book_info}}
                </p>
                <p class="fl" style="">
                  <span style="padding:2px 8px;display:inline-block;color:#00c98d; border:1px rgb(140, 228, 143) solid;margin-right:6px;border-radius:4px;"
                    :style="{color:item.stat_name=='连载'?'#70a7e3':'#00c98d',border:item.stat_name=='连载'?'1px #70a7e3 solid':'1px #00c98d solid'}">{{item.stat_name}}</span>
                  <span style="padding:2px 8px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">{{item.class_name}}</span>
                </p>
              </div>
            </router-link>
          </div>
      </div>
    </div>
    <div style="padding-left:18px;border-bottom:1px #f0f0f0 solid;">
        <div v-show="index>0" v-for="(item, index) in arr4"
            style="padding:10px 20px 10px 0;border-top:1px #f0f0f0 solid;" :key="index">
          <router-link :to="'/all/'+item.bid">
            <div class="clearfix">
              <p :style="{color:index==2 || index==1?'orange':'#ccc'}" class="fl" style="font-weight:bold;font-size:16px;padding-right:10px;">{{index+1}}</p>
              <p class="fl" style="font-size:14px;margin-bottom:10px;width:50%;text-overflow: ellipsis;overflow: hidden;white-space: nowrap;">{{item.bookname}}</p>
              <p class="fr" style="">
                <span style="padding:2px 8px;display:inline-block;color:#00c98d; border:1px rgb(140, 228, 143) solid;margin-right:6px;border-radius:4px;"
                  :style="{color:item.stat_name=='连载'?'#70a7e3':'#00c98d',border:item.stat_name=='连载'?'1px #70a7e3 solid':'1px #00c98d solid'}">{{item.stat_name}}</span>
                <span style="padding:2px 8px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">{{item.class_name}}</span>
              </p>
            </div>
          </router-link>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  name:'',
  props:['lee4_arr'],
  data(){
   return {
       arr4:this.lee4_arr,
   }
  },
  watch:{
    //一定要监听
    lee4_arr(a,b){
      this.arr4=a;
    }
  }
}
</script>

<style scoped>
.item_box{
  flex:1;
  margin:0 10px;
  width:25%;
  padding-bottom:30px;
}
.jinp h3{
  font-weight:normal;
  padding:24px 30px;
  position:relative;
}
.jinp h3:after{
  content:'';
  height:30px;
  width:6px;
  background-color: #2dc033;
  left:0;
  top:38%;
  position:absolute;
}

</style>



