<!-- 首页背景 -->
<template>
    <div id="ahomebj">
        <img src="http://goss1.cfp.cn/creative/vcg/800/new/VCG211241801901.jpg" alt="">
        <div class="jishu" style="color:#fff;">
            <span style="color:tomato;padding-right:6px;">{{num}}s</span>|
            <span @touchstart="leapfrog">跳过</span>
        </div>
    </div>
</template>

<script>
export default {
  name:'ahomebj',
  data(){
   return {
       num:5,
       timer:null,
   }
  },
  methods:{
      leapfrog(){
          this.$router.push({name:'Hello'});
          clearInterval(this.timer);
          this.timer=null;
      }
  },
  created(){
      this.timer=setInterval(()=>{
          this.num--;
          if(this.num<0){
              this.leapfrog();
              clearInterval(this.timer);
              this.timer=null;
          }
      },1000)
  }
}
</script>

<style scoped>
#ahomebj{
  position: fixed;
  left:0;
  top:0;
  height:100vh;
  width:100vw;
  z-index: 1000;
}
#ahomebj img{
  height:100%;
  width:100%;
}
.jishu{
    position:absolute;
    right:20px;
    top:30px;
    background-color: rgba(255,255,255,.3);
    padding:6px 16px;
    border-radius: 18px;
    font-size:28px;
}
.jishu span{
    padding:0 4px;
}
</style>



