<!-- 女生男生美文模块 -->
<template>
  <div class="jinp">
    <div style="min-height:160px;">
      <div class="clearfix" style="display:flex;padding:0 10px;">
          <div class="item_box fl" v-for="(item, index) in arr1" :key="index">
            <router-link :to="'/all/'+item.bid">
              <img :src="item.book_cover" style="height:120px;width:100%;" alt="">
              <p style="padding:4px 0;width:80px;text-overflow: ellipsis;overflow: hidden;white-space: nowrap;">{{item.bookname}}</p>
              <p style="color:#666;">{{item.author_name}}</p>
            </router-link>
          </div>
      </div>
    </div>
    <div style="padding-left:18px;border-bottom:1px #f0f0f0 solid;">
        <div v-for="(item, index) in arr1"
            style="padding:10px 20px 10px 0;border-top:1px #f0f0f0 solid;" :key="index">
          <router-link :to="'/all/'+item.bid">
            <div class="clearfix">
              <p class="fl" style="font-size:14px;margin-bottom:10px;width:60%;text-overflow: ellipsis;overflow: hidden;white-space: nowrap;">{{item.bookname}}</p>
              <p class="fr" style="">
                <span style="padding:2px 8px;display:inline-block;color:#00c98d; border:1px rgb(140, 228, 143) solid;margin-right:6px;border-radius:4px;"
                  :style="{color:item.stat_name=='连载'?'#70a7e3':'#00c98d',border:item.stat_name=='连载'?'1px #70a7e3 solid':'1px #00c98d solid'}">{{item.stat_name}}</span>
                <span style="padding:2px 8px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">{{item.class_name}}</span>
              </p>
            </div>
            <p style="color:#666;width:94%;text-overflow: ellipsis;overflow: hidden;white-space: nowrap;">{{item.book_info}}</p>
          </router-link>
        </div>
    </div>
    
  </div>
</template>

<script>
export default {
  name:'',
  props:['lee3_arr'],
  data(){
   return {
       arr1:this.lee3_arr,
   }
  },
  watch:{
    //一定要监听
    lee3_arr(a,b){
      this.arr1=a;
    }
  }
}
</script>

<style scoped>
.item_box{
  flex:1;
  margin:0 10px;
  width:25%;
  padding-bottom:30px;
}
.jinp h3{
  font-weight:normal;
  padding:24px 30px;
  position:relative;
}
.jinp h3:after{
  content:'';
  height:30px;
  width:6px;
  background-color: #2dc033;
  left:0;
  top:38%;
  position:absolute;
}

</style>



