<template>
  <div id="all">
    <!-- 自己写的弹出框 -->
    <div v-if="type" id="bj">
      <div>
        <p>离线下载本书</p>
        <p style="font-size:15px;">请先下载书旗小说客户端</p>
        <p class="search_btn">
          <a href="http://t.shuqi.com/?#!/ac/in/ct/download" style="color:#fff;">
            <van-button type="primary" style="margin-right:10px;">
              去下载
            </van-button>
          </a>
          <van-button type="primary" @touchstart="type=!type">暂不下载</van-button>
        </p>
      </div>
    </div>
    <!-- 头部 -->
    <div class="all_head">
      <van-icon @touchstart="returns" name="arrow-left" class="fl" style="padding:0 4px;"/>
      <!-- <router-link to="/"></router-link> -->
      <router-link to="/Hello"><van-icon name="home-o" class="fr"/></router-link>
      <h3 style="margin:0 100px;text-align:center;">书籍详情</h3>
    </div>
    <!-- 内容 -->
    <div>
      <!-- 上-个人 -->
      <div class="con_t" style="border-bottom:10px #f0f0f0 solid;">
        <div class="clearfix" style="margin-bottom:10px;">
          <div class="fl"><img class="img_s" :src="json.imgUrl" alt=""></div>
          <div class="fl">
            <h4 style="font-size:18px;padding:10px 0;font-weight:normal;">{{json.bookName}}</h4>
            <P class="al_p">{{json.authorName}}</P>
            <P class="al_p">
              <span>{{json.className}}</span>
              <span>{{json.wordCount}}万字</span>
            </P>
            <P class="al_p">正在免费中</P>
          </div>
        </div>
        <div class="con_btn">
          <van-button type="primary">开始阅读</van-button>
          <van-button type="primary" @touchstart="type=!type">离线下载</van-button>
          <van-button v-if="types2" type="primary" style="border:none;background:#f0f0f0;color:#666;">已添加</van-button>
          <van-button @touchstart="types2=!types2" v-else type="primary">加书架</van-button>
        </div>
        <p :style="{height:types?'auto':'83px'}" class="con_info">{{json.desc}}
          <span class="con_span" @touchstart="types=!types"><van-icon name="arrow-down"/></span>
        </p>
        <div class="con_two" style="border-bottom:1px #f0f0f0 solid;">
          <van-icon name="underway-o" style="font-size:24px;vertical-align: middle;"/>
          <span style="padding:0 10px;font-size:15px;">最新</span>
          <span style="color:#666;font-size:13px;">{{info}}</span>
          <van-icon name="arrow" class="fr" style="margin-top:4px;"/>
        </div>
        <div class="con_two">
          <van-icon name="coupon-o" style="font-size:24px;vertical-align: middle;"/>
          <span style="padding:0 10px;font-size:15px;">目录</span>
          <span style="color:#666;font-size:13px;">8小时前更新</span>
          <van-icon name="arrow" class="fr" style="margin-top:4px;"/>
        </div>
      </div>
      <!-- 中-评论 -->
      <div style="background:#fff;">
        <div class="jinp" style="border-bottom:1px #f0f0f0 solid;">
          <h3>精华评论</h3>
          <div style="padding-left:10px;">
            <div class="clearfix" v-for="(item, index) in arr" :key="index"
              :style="{borderBottom:index<2?'1px #f0f0f0 solid':'none'}"
              style="padding:14px 0;">
              <div class="fl" style="width:10%;margin-right:6px;"><img style="border-radius:50%;height:26px;width:26px;" :src="item.userPhoto" alt=""></div>
              <div class="fl" style="width:80%;">
                <p style="color:#666;">{{item.nickName}}</p>
                <p style="font-size:14px;line-height:24px;height:54px;overflow:auto;">{{item.text}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 还在看 -->
      <div style="background:#fff;margin-top:10px;">
        <div class="jinp">
          <h3>看过这本书的人还在看</h3>
          <div style="min-height:200px;">
            <lee1 :arr_list="arrImg1"></lee1>
            <lee1 :arr_list="arrImg2"></lee1>
          </div>
        </div>
        <div class="more" @touchstart="moreImg">
          换一换<van-icon name="replay" style="vertical-align: middle;padding-left:6px;" />
        </div>
      </div>
      <!-- 作者的其他作品 -->
      <div style="background:#fff;margin-bottom:10px;">
        <div class="jinp">
          <h3>作者的其他作品</h3>
          <div style="min-height:200px;">
            <lee2 :lee2_arr="arr1"></lee2>
          </div>
        </div>
      </div>
      <!-- 图片相关信息 -->
      <div style="background:#fff;margin-bottom:10px;">
        <div class="jinp">
          <h3>图片相关信息
            <span @touchstart="types1=!types1"><van-icon name="arrow-down" class="fr"/></span> 
          </h3>
          <div :style="{height:types1?'auto':'0'}" style="overflow:auto;">
            <p style="padding:0 14px;font-size:15px;color:#666;">版权来源：<span style="color:#2dc033;">书旗小说</span></p>
            <p style="padding:8px 14px;font-size:15px;color:#666;">免责声明：本书的数字版权由原创版权提供，授权本软件使用，制作,发行，若包含不良信息，请及时告知客服。</p>
          </div>
        </div>
      </div>
    </div>
    <!--尾部  -->
    <div style="background:#fff;">
      <footers></footers>
    </div>
  </div>
</template>

<script>
export default {
  name:'all',
  data(){
   return {
    type:false,
    uid:'',
    json:'',
    info:'',
    types:false,
    arr:'',//评论
    arrImg1:'',//书图片
    arrImg2:'',//书图片
    arr1:'',//作者其他作品
    types1:false,
    types2:false,
   }
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
  created() {
    //拿数据 传bid 
    this.uid=this.$route.params.id;
    //上-个人
    this.$http.post('http://walden1.shuqireader.com/webapi/book/info/?',{
      bookId: 7884683,  
      user_id: 8000000,
      timestamp: 1568293822678,
      //这里bid是一个死值 接口坏 需要一个sign验证
      sign: 'd729a322e07232e7ac44a23cd028de0f', 
      shuqi_h5:'',
    },{
      emulateJSON:true
    }).then((res)=>{
      this.json=res.data.data;
      this.info=res.data.data.lastChapter.chapterName;
    })  
    //评论
    this.$http.get('http://read.xiaoshuo1-sm.com/novel/i.php?do=sp_get&authorId=30589&bookId='+this.uid+'&fetch=merge&sqUid=8000000&source=store&size=3&page=1&shuqi_h5=&_=1568295403129').then((res)=>{
      this.arr=res.data.data;
    })  
    //看过这本书的人还在看
    this.$http.get('http://bookapi.shuqiapi.com/?',{
      params:{  //这里bid是一个死值 接口坏 需要一个sign验证
        bamp: 'sqdk',
        dataFrom: 'sm',
        bid: 7884683,
        bkName: '薛小苒的古代搭伙之旅',
        authName: '千墨',
        authorid: 30589,
        limit: 8,
        ver: '08.23.17.12',
        fr_pr_id: 10002,
        tk: 'Nzg4NDY4MzQxN2FjNTllOWYzMDU4OQ%3D%3D',
        shuqi_h5: '',
        _: 1568296318390,
      }
    }).then((res)=>{
      this.arrImg1=res.data.data.dk.bookinfo.splice(0,4);
      this.arrImg2=res.data.data.dk.bookinfo;
    })  
    //作者其他作品
    this.$http.get('http://bookapi.shuqiapi.com/?bamp=sqat&authorname=%E5%8D%83%E5%A2%A8&authorid=30589&limit=4&atType=click&fr_pr_id=10001&tk=MzA1ODk0MTdhYzU5ZTlm&shuqi_h5=&_=1568295148524').then((res)=>{
      this.arr1=res.data.data.at.bookinfo;
      console.log(this.arr1)
    })  
  },
  methods:{
    moreImg(){
      this.$http.get('http://bookapi.shuqiapi.com/?bamp=sqdk&dataFrom=sm&bid=7884683&bkName=%E8%96%9B%E5%B0%8F%E8%8B%92%E7%9A%84%E5%8F%A4%E4%BB%A3%E6%90%AD%E4%BC%99%E4%B9%8B%E6%97%85&authName=%E5%8D%83%E5%A2%A8&authorid=30589&limit=8&ver=08.23.17.12&fr_pr_id=10002&tk=Nzg4NDY4MzQxN2FjNTllOWYzMDU4OQ%253D%253D&shuqi_h5=&_=1568297729825').then((res)=>{
        this.arrImg1=res.data.data.dk.bookinfo.splice(0,4);
        this.arrImg2=res.data.data.dk.bookinfo;
      })  
    },
    returns(){
      console.log(3)
      this.$router.go(-1);
    },
  }
}
</script>

<style scoped>
#all{
  min-height:100vh;
  position:relative;
  background-color: #f0f0f0;
  z-index: 102;
  padding-top:80px;
}
.all_head{
  padding:0 20px;
  height:80px;
  line-height:80px;
  position:fixed;
  left:0;top:0;
  width:100%;
  z-index: 20;
  box-sizing: border-box;
  border-bottom:1px #ccc solid;
  background-color: #fff;
}
.all_head h3{
  font-size:36px;
  font-weight:normal;
}
.all_head i{
  line-height:80px;
  font-size:40px;
  color:#2dc033;
}
.con_t{
  background-color: #fff;
  padding:30px;
  padding-bottom:0;
}
.img_s{
  height:250px;
  width:180px;
  margin-right:34px;
}
.al_p{
  color:#666;
  line-height:50px;
  font-size:28px;
}
.con_btn button{
  width:29%;
  height:80px;
  font-size:30px;
  margin:0 10px;
  border-radius: 14px;
}
.con_btn button:nth-of-type(2), .con_btn button:nth-of-type(3){
  background-color: #fff;
  color:#00c98d;
}
.con_info{
  font-size:28px;
  padding-top:30px;
  color:#666;
  line-height:40px;
  overflow: hidden;
  position:relative;
  margin-bottom:30px;
}
.con_span{
  position:absolute;
  bottom:0;
  right:20px;
  z-index: 10;
  height:70px;
  width:60px;
  border-radius: 20px;
  text-align:center;
  display: block;
  background-color: rgba(255,255,255,.9);
}
.con_span i{
  font-size:40px;
  line-height:70px;
}
.con_two{
  padding:40px 0;
}
.jinp h3{
  font-weight:normal;
  padding:24px 30px;
  position:relative;
}
.jinp h3:after{
  content:'';
  height:30px;
  width:6px;
  background-color: #2dc033;
  left:0;
  top:38%;
  position:absolute;
}
.more{
  height:100px;
  text-align:center;
  font-size:30px;
  line-height:100px;
  border-top:1px #ccc solid;
  border-bottom:14px #f0f0f0 solid;
  color:#666;
}
.search_btn{
    margin-top:30px;
}
.search_btn button{
    width:47%;
    height:70px;
    border-radius: 12px;
    font-size:30px;
}
.search_btn button:nth-of-type(2){
    background-color: #fff;
    color:#2dc033;
}
#bj{
  height:100vh;
  width:100vw;
  position: fixed;
  left:0;
  top:0;
  z-index: 100;
  background-color: rgba(0,0,0,.5);
}
#bj>div{
  position:absolute;
  left:50%;
  top:50%;
  height:300px;
  width:600px;
  margin-left:-300px;
  margin-top:-150px;
  background-color: #fff;
  padding:30px;
  box-sizing: border-box;
  text-align: center;
}
#bj>div p{
  font-size:34px;
  line-height:56px;
}


</style>



