<template>
  <div id="apub5pg">
    <!-- 头部 -->
    <div class="apnub5_head">
      <router-link to="/apub5"><van-icon name="arrow-left" class="fl" style="padding:0 4px;"/></router-link>
      <router-link to="/Hello"><van-icon name="home-o" class="fr"/></router-link>
      <h3>{{title}}</h3>
    </div>
    <!-- 内容 -->
    <div>
      <p style="padding:16px;font-size:14px;color:#666;">{{info}}</p>
    </div>
    <div style="background:#fff;">
      <div v-for="(item, $index) in arr" :key="$index" class="con_t">
        <router-link :to="'/all/'+item.bid">
          <div class="clearfix">
            <div class="fl"><img class="img_s" :src="item.cover" alt=""></div>
            <div class="fl">
              <h4 style="font-size:18px;padding-bottom:5px;font-weight:normal;">{{item.title}}</h4>
              <P class="al_p" style="margin-bottom:5px;">{{item.author}}</P>
              <p class="fl" style="">
                <span style="padding:2px 8px;display:inline-block;color:#2dc033;border:1px #2dc033 solid;border-radius:4px;">完集</span>
                <span style="padding:2px 8px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">30万字</span>
                <span style="padding:2px 8px;display:inline -block;color:#ccc;border:1px #ccc solid;border-radius:4px;">古风</span>
              </p>
            </div>
          </div>
        </router-link>
        <p :style="{height:index==$index?'auto':'83px'}" class="con_info">{{item.desc}}
          <span class="con_span" @touchstart="kan($index)"><van-icon name="arrow-down"/></span>
        </p>
        <p style="line-height:24px;color:#bbb8b8;font-size:14px;padding-bottom:10px;"><span>书友推荐</span>{{item.recomment}}</p>
      </div>
    </div>
    <!--尾部  -->
    <div>
      <footers></footers>
    </div>
  </div>
</template>

<script>
export default {
  name:'apub5pg',
  data(){
   return {
     arr:'',
     title:'',
     info:'',
     cid:'',
     types:false,
     index:-1,
   }
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
  created() {
    this.cid=this.$route.params.v;
    
    this.$http.get('http://read.xiaoshuo1-sm.com/novel/i.php?',{
      params:{
        do: 'is_pay_sddetail',
        sdid:this.cid,
        p: 1,
        shuqi_h5:'' ,
        onlyCpBooks: 1,
        _: 1568351563227,
      }
    }).then((res)=>{
      this.title=res.data.info.title;
      this.info=res.data.info.sdesc;
      this.arr=res.data.data;
      console.log(res.data.data)
    })  
  },
  methods:{
    kan(i){
      if(this.index==i){
        this.index=-1;
      }else{
        this.index=i;
      }
    }
  }
}
</script>

<style scoped>
#apub5pg{
  min-height:100vh;
  position:relative;
  background-color: #f0f0f0;
  z-index: 102;
  padding-top:80px;
}
.apnub5_head{
  padding:0 20px;
  height:80px;
  line-height:80px;
  position:fixed;
  left:0;top:0;
  width:100%;
  z-index: 20;
  box-sizing: border-box;
  border-bottom:1px #ccc solid;
  background-color: #fff;
}
.apnub5_head h3{
  font-size:36px;
  font-weight:normal;
  margin:0 80px;
  width:80%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow:hidden;
  text-align:center;
}
.apnub5_head i{
  line-height:80px;
  font-size:40px;
  color:#2dc033;
}
.con_t{
  background-color: #fff;
  border-bottom:1px #f0f0f0 solid;
  padding:30px;
  padding-bottom:0;
}
.img_s{
  height:160px;
  width:120px;
  margin-right:34px;
}
.al_p{
  color:#666;
  line-height:50px;
  font-size:28px;
}
.con_info{
  font-size:28px;
  padding-top:30px;
  color:#666;
  line-height:40px;
  overflow: hidden;
  position:relative;
  margin-bottom:16px;
}
.con_span{
  position:absolute;
  bottom:0;
  right:20px;
  z-index: 10;
  height:70px;
  width:60px;
  border-radius: 20px;
  text-align:center;
  display: block;
  background-color: rgba(255,255,255,.9);
}
.con_span i{
  font-size:40px;
  line-height:70px;
}
</style>



