<!-- 精品书单模块 -->
<template>
  <div id="lee5">
    <div style="background:#fff;padding-left:16px;">
      <div v-for="(item, index) in arr" :key="index" class="apub5_item clearfix">
        <router-link :to="{path:'/apub5pg/'+item.id}">
          <div class="fl" style="width:65%;">
            <h5 style="margin-bottom:16px;font-size:16px;font-weight:normal;">{{item.title}}</h5>
            <p style="width:230px;color:#666;margin-bottom:14px;height:34px; overflow:auto;">{{item.sdesc}}</p>
            <p class="fl" style="">
              <span style="padding:2px 8px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">言情</span>
              <span style="padding:2px 8px;display:inline-block;color:#ccc;border:1px #ccc solid;border-radius:4px;">穿越</span>
              <span style="padding:2px 8px;display:inline -block;color:#ccc;border:1px #ccc solid;border-radius:4px;">古风</span>
            </p>
          </div>
          <div class="fr apub5_img">
            <img :src="item.items[0].cover" alt="">
            <img :src="item.items[1].cover" alt="">
            <img :src="item.items[2].cover" alt="">
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name:'lee5',
  props:['lee5_arr'],
  data(){
   return {
       arr:this.lee5_arr,
   }
  },
  watch:{
    //一定要监听
    lee5_arr(a,b){
      this.arr=a;
    }
  }
}
</script>

<style scoped>
.apub5_item{
  padding:40px 0;
  border-bottom:1px #ccc solid;
}
.apub5_img{
  position:relative;
  overflow: hidden;
  height:210px;
  width:26%;
  margin-right:20px;
}
.apub5_img img:nth-of-type(1){
  height:70%;
  width:80%;
  left:0;
  bottom:0;
  position: absolute;
  z-index: 0;
}
.apub5_img img:nth-of-type(2){
  height:90%;
  width:80%;
  left:10%;
  top:10%;
  position: absolute;
  z-index: 1;
}
.apub5_img img:nth-of-type(3){
  height:70%;
  width:80%;
  right:0;
  bottom:0;
  position: absolute;
  z-index: 0;
}
</style>



