<template>
  <div id="apub2">
    <!-- 头部 -->
    <div class="apnub2_head">
      <router-link to="/Hello"><van-icon name="arrow-left" class="fl" style="padding:0 4px;"/></router-link>
      <router-link to="/Hello"><van-icon name="home-o" class="fr"/></router-link>
      <h3 style="margin:0 100px;text-align:center;">排行</h3>
    </div>
    <!-- 内容 -->
    <div style="min-height:60vh;">
      <div class="apub2_item" v-show="item.m_s_name" v-for="(item, index) in arr" :key="index">
        <router-link :to="{path:'/apub2pg/'+item.orderid,query:{title:item.content.title}}">
          <div :style="{backgroundColor:random()}" class="bj_apub fl"></div>
          <div>
            <p style="font-size:16px;margin-bottom:6px;">{{item.content.title}}</p>
            <p style="color:#666;">{{item.content.desc}}</p>
          </div>
        </router-link>
      </div>
    </div> 
    <!--尾部  -->
    <div>
      <footers></footers>
    </div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui'; //局部引入弹出框
export default {
  name:'apub2',
  data(){
   return {
     arr:'',
   }
  },
  mounted(){
    //进入页面关掉footer
    this.$router.app.$el.querySelector('.footer').style.display='none';
  },
  created() {
    //进入页面加载loading
    Indicator.open({
      text: '加载中...',
      spinnerType: 'snake'
    });
    this.$http.get('http://bookstoreapi.shuqireader.com/eva_bookstore/v1/stencil/query?appId=1&pageId=4&channelId=&versionId=&ver=&shuqi_h5=&md5key=&userId=8000000&timestamp=1568269571&type=2&resetcache=&sign=D62B4EFDAD8E6314AD174C6148440668&key=shuqiapi&_=1568269571046').then((res)=>{
      this.arr=res.data.data.module;
      setTimeout(() => {
        Indicator.close(); //关闭loading
      }, 300);
    })  
  },
  methods:{
    random(){
      return `rgb(${parseInt(Math.random()*255)},${parseInt(Math.random()*255)},${parseInt(Math.random()*255)})`;
    }
  }
}
</script>

<style scoped>
#apub2{
  min-height:100vh;
  position:relative;
  background-color: #f0f0f0;
  z-index: 102;
  padding-top:80px;
}
.apnub2_head{
  padding:0 20px;
  height:80px;
  line-height:80px;
  position:fixed;
  left:0;top:0;
  width:100%;
  box-sizing: border-box;
  border-bottom:1px #ccc solid;
  background-color: #fff;
}
.apnub2_head h3{
  font-size:36px;
  font-weight:normal;
}
.apnub2_head i{
  line-height:80px;
  font-size:40px;
  color:#2dc033;
}
.apub2_item{
  padding:60px 20px;
  background-color: #fff;
  margin-bottom:20px;

}
.bj_apub{
  height:70px;
  width:70px;
  background-color: tomato;
  border-radius: 50%;
  margin-right:26px;
  margin-top:8px;
}

</style>



